import React, { useContext, useState } from 'react';
import * as dayjs from 'dayjs';
import { TableCell, TableBody, TableRow } from '@windmill/react-ui';
import {  FiTrash2 } from 'react-icons/fi';

import Tooltip from '../tooltip/Tooltip';
import MainModal from '../modal/MainModal';
import { SidebarContext } from '../../context/SidebarContext';
import { Link } from 'react-router-dom';

const EnquiryTable = ({ enquiries }) => {
    const [enquiryId, setEnquiryId] = useState('');
    const { toggleModal } = useContext(SidebarContext);

    const handleModalOpen = (id) => {
        setEnquiryId(id);
        toggleModal();
    };

    return (
        <>
            <MainModal id={enquiryId} />
            <TableBody>
                {enquiries?.map((enquiry) => (
                    <TableRow key={enquiry._id}>
                        <TableCell>
                            <span className="font-semibold uppercase text-xs">
                                {' '}
                                {enquiry._id.substring(20, 24)}
                            </span>
                        </TableCell>
                        <TableCell>
                            <span className="text-sm">
                                {dayjs(enquiry.created_at).format('MMM D, YYYY')}
                            </span>
                        </TableCell>
                        <TableCell>
                            <span className="text-sm">{enquiry.fullname}</span>
                        </TableCell>
                        <TableCell>
                            <span className="text-sm">{enquiry.email}</span>{' '}
                        </TableCell>
                        <TableCell>
                            <span className="text-sm font-medium">{enquiry.phoneno}</span>
                        </TableCell>
                        <TableCell>
                            <Link className="text-sm font-medium" to={`/product/${enquiry.product_id}`}>
                                <label>{enquiry.product_name}</label>
                                <label>{enquiry.product_price}</label>
                                <img src={enquiry.product_image} alt={enquiry.product_name}
                                    width="50px" height="50px" style={{ objectFit: "cover" }} />
                            </Link>
                        </TableCell>
                        <TableCell>
                            <span className="text-sm">
                                {enquiry.message}
                            </span>
                        </TableCell>

                        <TableCell>
                            <div className="flex justify-end text-right">
                                <div
                                    onClick={() => handleModalOpen(enquiry._id)}
                                    className="p-2 cursor-pointer text-gray-400 hover:text-red-600"
                                >
                                    <Tooltip
                                        id="delete"
                                        Icon={FiTrash2}
                                        title="Delete"
                                        bgColor="#F87171"
                                    />
                                </div>
                            </div>
                        </TableCell>
                    </TableRow>
                ))}
            </TableBody>
        </>
    );
};

export default EnquiryTable;
